@import '../../style/variables';

.book {
    &__inner {
      display: grid;
      justify-content: center;
      grid-template-columns: repeat(3, 225px);
      gap: 25px;
    }
    &__item {
      transition: 0.3s transform;
      &-img {
        box-shadow: 3px 4px 15px 2px rgba(0, 0, 0, 0.25);
        width: 225px;
        height: 350px;
        object-fit: cover;
      }
      &-name {
        margin-top: 5px;
        font-weight: 500;
        font-size: 18px;
        line-height: 18px;
        text-align: center;
        color: $main-color;
      }

      &:hover {
          transform: translateY(-5px);
      }
    }
}

@media (max-width: 900px) {
  .book {
    &__inner {
      grid-template-columns: repeat(3, 216px);
    }
    &__item {
      &_img {
        width: 216px;
      }
    }
  }
}

@media (max-width: 768px) {
  .book {
    &__inner {
      grid-template-columns: repeat(3, 156px);
      gap: 15px;
    }
    &__item {
      &-img {
        width: 156px;
        height: 250px;
      }
    }
  }
}

@media (max-width: 425px) {
  .book {
    &__inner {
      grid-template-columns: repeat(2, 167px);
    }
    &__item {
      &-img {
        width: 167px;
        height: 260px;
      }
    }
  }
}